* {
  font-family: "Spoqa Han Sans", "Spoqa Han Sans JP", "Sans-serif";
  outline: none;
  box-sizing: border-box;
}

body {
  background-color: #f8f9fa;
  text-align: center;
}

h1 {
  margin: 100px 0 4px 0;
  font-size: 40pt;
  color: #343a40;
}

h2 {
  margin: 0 0 60px 0;
  font-size: 18pt;
  font-weight: normal;
  color: #868e96;
}

input[type="text"] {
  padding: 15px 20px;
  width: 500px;
  border: 2px solid #ced4da;
  font-size: 15pt;
  text-align: center;
}

.checkbox-wrapper {
  margin: 15px 0;
}

.checkbox-wrapper label {
  margin-right: 15px;
}

.checkbox-wrapper label:last-child {
  margin-right: 0;
}

.result {
  margin: 0 auto;
  padding: 15px 20px;
  width: 500px;
  background-color: #e9ecef;
  color: #495057;
  font-size: 15pt;
  text-align: center;
}
